<template>
	<div id="resume">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #000000 !important;"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">简历</div>
      </div>
    </div>
    <div class="login_box">
      <div class="background_color_ffffff" style="padding: 5px 12px;">
        <img :src="resumeInfo.user.userimg||'static/img/img_tx_zp.png'" style="width: 15%;vertical-align: text-top;"/>
        <div style="display: inline-block;width: 80%;">
          <span>{{resumeInfo.title}}</span>
          <span class="font_12 float_right color_8b8b8b">发布时间:{{resumeInfo.addtime}}</span>
        </div>
        <span style="position: relative;left: 17%;bottom: 20px;" class="font_12 color_8b8b8b">{{resumeInfo.uname}}</span>
        <div class="login_bar background_color_ffffff login_bottom" style="padding: 10px;display: flex;align-items: center;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">头像</span>
          <img :src="resumeInfo.sameimg" style="width: 50px;height: 50px" @click="show = true"/>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">性别</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.sex}}</span>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">学历</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.xueli}}</span>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">期望薪资</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.price}}</span>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">期望职位</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.title}}</span>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">工作年限</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.age}}</span>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">工作经历</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.content}}</span>
        </div>
        <div class="login_bar background_color_ffffff login_bottom" style="border-top: 1px solid #eeeeee;padding: 10px;">
          <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">联系方式</span>
          <span class="color_8b8b8b font_12">{{resumeInfo.tell}}</span>
        </div>
      </div>
    </div>
    <div class="van-modal" style="z-index: 2001;" @click="show = false" v-show="show">
      <img :src="resumeInfo.sameimg" style="width: 80%;margin-top: 100px;margin-left: 10%;"/>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'resume',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        samecityId:'',
        resumeInfo:[],
        show : false,
			}
		},
    mounted() {
      this.samecityId = this.$store.state.resumeInfo.samecityid;
      this.getResumeInfo();
    },
		methods:{
      getResumeInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:$this.samecityId,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecityinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.resumeInfo = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .login_box{
    margin-top: 50px;
  }
  .header {
    box-shadow: 0px 2px 10px lightgrey;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 99;
  }
</style>



